
import { defineComponent } from 'vue'

export default defineComponent({
    name: 'TheServices',

    setup() {
        const getIcon = (n: number): string =>
            require(`@/assets/images/services-icons/${n}.svg`)

        return {
            data: [
                {
                    imgSrc: getIcon(1),
                    title: '',
                    desc: '       ',
                },
                {
                    imgSrc: getIcon(2),
                    title: '',
                    desc: '   : , ,   ,   ',
                },
                {
                    imgSrc: getIcon(3),
                    title: '- ',
                    desc: '    :       ',
                },
            ],
        }
    },
})
